/**
 * Animate
 */

// Animates elements when scrolled into view.
// When page loads, animation classes are removed from elements
// then added back when scrolled into view.

// TODO: for infinite animations, keep observer active
//    and disable animation when out of view

const ANIMATE_SELECTOR = "[class*=animate-]"

interface AnimateElem extends HTMLElement {
  dataset: {
    animate: string
  }
}

const observer = new IntersectionObserver((entries) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      const elem = entry.target as AnimateElem
      elem.classList.add(...elem.dataset.animate.split(","))
      observer.unobserve(elem)
    }
  })
})

const processElem = async (elem: AnimateElem) => {
  const animClasses: Array<string> = []
  elem.classList.forEach((cls) => {
    if (cls.substring(0, 8) === "animate-") {
      animClasses.push(cls)
    }
  })
  elem.dataset.animate = animClasses.join(",")
  elem.classList.remove(...animClasses)
  observer.observe(elem)
}

const run = () => {
  document.querySelectorAll<AnimateElem>(ANIMATE_SELECTOR).forEach((elem) => {
    processElem(elem)
  })
}

const init = async () => {
  if (
    document.readyState === "complete" ||
    document.readyState === "interactive"
  ) {
    setTimeout(run, 10)
  } else {
    document.addEventListener("DOMContentLoaded", () => {
      setTimeout(run, 10)
    })
  }
}

export default { init }
